<template>
  <div>
    <!-- 面包屑导航栏 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片区域 -->
    <el-card>
      <el-button type="primary">添加角色</el-button>
      <el-table :data="roleslist" border stripe>
        <!-- 展开列 -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row v-for="(item1,index1) in scope.row.children" :key="item1.id"
            :class="['bd_bottom',index1===0? 'bd_top':'','view_center']">
              <!-- 一级 -->
              <el-col :span="5">
                <el-tag closable @close="removeRole(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 二级&三级 -->
              <el-col :span="19">
                <!-- 二级循环 -->
                <el-row v-for="(item2,index2) in item1.children" :key="item2.id"
                :class="[index2===0? '':'bd_top','view_center']">
                  <!-- 左边渲染二级权限 -->
                  <el-col :span="6">
                    <el-tag closable type="success" @close="removeRole(scope.row,item2.id)">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!-- 右边渲染三级权限 -->
                  <el-col :span="18">
                    <el-tag closable type="warning"
                    v-for="item3 in item2.children" :key="item3.id"
                    @close="removeRole(scope.row,item3.id)">{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>

        <!-- 索引列 -->
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini">编辑</el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini">删除</el-button>
            <el-button type="warning" icon="el-icon-setting" size="mini" @click="showSettingDialog(scope.row)">分配权限</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- 分配权限对话框 -->
    <el-dialog
      title="分配权限"
      :visible.sync="settingDialogVisible"
      width="50%"
      @close="settingDialogClosed">
      <!-- 树形控件 -->
      <el-tree
      :data="rightsList"
      :props="treeProps"
      node-key="id"
      show-checkbox
      default-expand-all
      :default-checked-keys="defKeys"
      ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="settingDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotRights">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 角色列表信息
      roleslist: [],
      // 控制分配权限对话框的显示与隐藏
      settingDialogVisible: false,
      // 所有权限列表
      rightsList: [],
      // 树形控件的属性绑定对象
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      // 默认勾选的节点id（其实就是三级节点的id），存放在数组里面
      defKeys: [],
      // 当前操作的角色的id
      roleId: ''
    }
  },
  created () {
    this.getRolesList()
  },
  methods: {
    // 获取角色列表
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error('获取角色列表失败！')
      this.roleslist = res.data
      // console.log(this.roleslist)
    },
    // 根据id来删除角色指定的权限tag
    async removeRole (role, RightId) {
      const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${RightId}`)
      if (res.meta.status !== 200) return this.$message.error('删除权限失败！')
      this.$message.success('删除权限成功！')
      // console.log(role.children)
      // console.log(res.data)
      // 返回来的data是当前角色下，最新的权限数据
      // 保证数据更新，且不刷新页面，重新渲染列表
      role.children = res.data
    },
    // 点击分配权限按钮，显示分配权限对话框，并发起请求获取所有权限列表
    async showSettingDialog (role) {
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) return this.$message.error('获取权限列表失败！')
      this.rightsList = res.data
      // console.log(this.rightsList)

      // 获得三级节点的id,并通过绑定default-checked-keys="defKeys"勾选
      this.getLeafKeys(role, this.defKeys)
      this.settingDialogVisible = true
      this.roleId = role.id
    },
    // 获得所有三级节点的id，并保存到defKeys数组中
    getLeafKeys (node, arr) {
      // 当没有子节点，也就是找到三级节点时，
      // 将所有三级节点的id存到数组中
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      })
    },
    // 监听对话框的关闭事件，关闭时，清空defKeys数组里面的内容
    settingDialogClosed () {
      this.defKeys = []
    },
    // 点击确定按钮，为角色分配权限
    async allotRights () {
      // 获取选中和半选的id，并保存到数组中去
      const keys = [...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()]
      // console.log(keys)
      const keystr = keys.join(',')
      // console.log(keystr)

      // 发起请求，为角色授权
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: keystr })
      // console.log(res)
      if (res.meta.status !== 200) return this.$message.error('更新权限失败！')
      this.$message.success('更新权限成功！')
      this.getRolesList()
      this.settingDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag{
  margin: 7px;
}
.bd_top{
  border-top: 1px solid #eee;
}
.bd_bottom{
  border-bottom: 1px solid #eee;
}
// 居中显示
.view_center{
  display: flex;
  align-items: center;
}
</style>
